import { StatisticsTokens } from '@/apis/path'
import { useFetch } from '@cetus/hooks'
import { d, formatPrice, symbolDataDisplayProcessing } from '@cetus/utils'

type StatsTokensOrderBy = 'change_percentage' | '-change_percentage' | 'vol_24' | '-vol_24' | 'price' | '-price' | 'tvl' | '-tvl'
export type GetStatsTokensParams = {
  order_by?: StatsTokensOrderBy
  limit?: number
  offset?: number
}

export default function useStatsTokens() {
  const { fetchByApi } = useFetch()

  const getStatesTokens = async (params: GetStatsTokensParams) => {
    const res = await fetchByApi(StatisticsTokens, 'GET', params)
    console.log('🚀 ~ file: useStatsTokens.ts:14 ~ getStatesTokens ~ res:', res)

    if (res?.prices) {
      const tokensData = res?.prices?.map((item: any) => {
        const isPositive = d(item?.change_percentage).gt(0)
        return {
          coinType: item?.coin,
          price: '$' + formatPrice(item?.price),
          priceChange: (isPositive ? '+' : '') + symbolDataDisplayProcessing(item?.change_percentage, '%'),
          priceChangeColor: Number(item?.change_percentage) === 0 ? 'text_caption' : isPositive ? 'primary_green' : 'primary_red',
          volume24: symbolDataDisplayProcessing(item?.vol_24, '$'),
          tvl: symbolDataDisplayProcessing(item?.tvl, '$')
        }
      })

      console.log('🚀 ~ file: useStatsTokens.ts:20 ~ tokensData ~ tokensData:', tokensData)

      return {
        data: tokensData,
        total: res?.total
      }
    }

    return null
  }

  return {
    getStatesTokens
  }
}
