import { TransactionTxPath } from '@/apis/path'
import { bnAmountNumericAbbreviation } from '@/utils/api-data-utils'
import { useFetch } from '@cetus/hooks'
import { getTimeDifferenceString } from '@cetus/utils'

export type GetTransactionsTxParams = {
  coin: string
  tx_type?: 'all' | 'swap' | 'add' | 'remove'
  offset?: number
  limit?: number
}
export default function useTransactionsTx() {
  const { fetchByApi } = useFetch()

  const getTransactionsTx = async (params: GetTransactionsTxParams) => {
    const res = await fetchByApi(TransactionTxPath, 'GET', params)
    console.log('🚀 ~ file: useTransactionsTx.ts:14 ~ getTransactionsTx ~ res:', res)

    if (res?.tx_list) {
      const txData = res?.tx_list?.map((item: any) => {
        const decimalsA = item?.tokenA?.decimal
        const decimalsB = item?.tokenB?.decimal
        const a2b = item?.tx_type === 'swap' ? item?.a2b : true
        return {
          tokenA: a2b ? item?.tokenA : item?.tokenB,
          tokenB: a2b ? item?.tokenB : item?.tokenA,
          account: item?.account, // UI层可用addressAbridge处理
          tokenAmountA: a2b ? item?.token_a_amount : item?.token_b_amount, // UI层展示处理精度后可用bnAmountNumericAbbreviation处理
          tokenAmountB: a2b ? item?.token_b_amount : item?.token_a_amount, // UI层展示处理精度后可用bnAmountNumericAbbreviation处理
          displayTokenAmountA: a2b
            ? bnAmountNumericAbbreviation(item?.token_a_amount, decimalsA)
            : bnAmountNumericAbbreviation(item?.token_b_amount, decimalsB),
          displayTokenAmountB: a2b
            ? bnAmountNumericAbbreviation(item?.token_b_amount, decimalsB)
            : bnAmountNumericAbbreviation(item?.token_a_amount, decimalsA),
          txType: item?.tx_type,
          tx: item?.tx,
          event_seq: item?.event_seq,
          uniq_id: item?.uniq_id,
          timeDisplay: getTimeDifferenceString(item?.timestamp * 1000)
        }
      })

      return {
        data: txData,
        total: res?.total
      }
    }

    return null
  }

  return {
    getTransactionsTx
  }
}
