import { StatisticsPools } from '@/apis/path'
import { addApr, aprProcessing, feeDisplayProcessing } from '@/utils/api-data-utils'
import { useFetch } from '@cetus/hooks'
import { d, symbolDataDisplayProcessing } from '@cetus/utils'

export type GetStatsPoolsParams = {
  order_by?: string
  limit?: number
  offset?: number
}

export default function useStatsPools() {
  const { fetchByApi } = useFetch()

  const getStatsPools = async (params: GetStatsPoolsParams) => {
    const res = await fetchByApi(StatisticsPools, 'GET', params)
    console.log('🚀 ~ file: useStatsPools.ts:13 ~ getStatsPools ~ res:', res)
    if (res?.pools) {
      const poolsData = res?.pools?.map((item: any) => {
        const isReverse = item?.is_forward === false
        const tokenA = item?.coin_a
        const tokenB = item?.coin_b
        const displayTokenA = !isReverse ? tokenA : tokenB
        const displayTokenB = !isReverse ? tokenB : tokenA
        const name = `${displayTokenA?.symbol} - ${displayTokenB?.symbol}`

        const feeApr = aprProcessing(item?.apr_24h)
        const feeAprDisplay = aprProcessing(item?.apr_24h, true)

        // const haveMining = item?.has_mining 统计接口V3版本增加has_mining字段
        const haveMining =
          item?.rewarder?.is_display_rewarder &&
          (item?.rewarder?.rewarder_display1 ||
            item?.rewarder?.rewarder_display2 ||
            item?.rewarder?.rewarder_display3 ||
            item?.rewarder?.rewarder_display4 ||
            item?.rewarder?.rewarder_display5)
        const miningAprList =
          item?.rewarder?.rewarder_coin
            ?.filter((rewarder: any, index: number) => {
              // TODO 过滤掉奖励释放为0的奖励
              // const emissionsPerSecond = d(item?.fields?.emissions_per_second).mul(Decimal.pow(2, -64)).toString()
              // if (item?.rewarder?.[`rewarder_display${index + 1}`] && d(emissionsPerSecond).gt(0)) {
              if (d(aprProcessing(rewarder?.apr)).gt(0)) {
                return true
              }
              return false
            })
            ?.map((mItem: any) => {
              return {
                symbol: mItem?.symbol,
                apr: aprProcessing(mItem?.apr),
                aprDisplay: aprProcessing(mItem?.apr, true),
                coinType: mItem?.coin
              }
            }) || []

        const miningAprTotal = addApr(miningAprList.map((item: any) => item?.apr))
        const haveFarming = !!(
          item?.stable_farming &&
          (item?.stable_farming?.show_rewarder_1 || item?.stable_farming?.show_rewarder_2 || item?.stable_farming?.show_rewarder_3) &&
          item?.stable_farming?.stable_rewarder
        )

        const farmingApr = addApr(item?.stable_farming?.stable_rewarder?.map((item: any) => item?.apr))
        const farmingAprDisplay = symbolDataDisplayProcessing(d(farmingApr).mul(100).toString(), '%', 2, true)

        const feeAndMiningApr = addApr([feeApr, miningAprTotal])
        const feeAndMiningAprDisplay = d(feeAndMiningApr).gt(10000)
          ? '>1,000,000%'
          : symbolDataDisplayProcessing(d(feeAndMiningApr).mul(100).toString(), '%')

        const totalAprDisplay = addApr([feeAndMiningApr, farmingApr], true)

        const haveVaults = !!item?.auto_pool

        return {
          name,
          tokenA,
          tokenB,
          displayTokenA,
          displayTokenB,
          poolAddress: item?.swap_account,
          tvl: symbolDataDisplayProcessing(item?.pure_tvl_in_usd, '$'),
          volume7day: symbolDataDisplayProcessing(item?.vol_in_usd_7_day, '$'),
          volume24h: symbolDataDisplayProcessing(item?.vol_in_usd_24h, '$'),
          fees24: symbolDataDisplayProcessing(item?.fee_24_h, '$'),
          haveMining: haveMining && miningAprList?.length > 0,
          haveFarming,
          haveVaults,
          miningAprList,
          feeAprDisplay,
          feeApr,
          feeAndMiningAprDisplay,
          farmingAprDisplay,
          totalAprDisplay,
          feeDisplay: feeDisplayProcessing(item?.fee)
        }
      })
      console.log('🚀 ~ file: useStatsPools.ts:13 ~ getStatsPools ~ poolsData:', poolsData)

      return {
        data: poolsData,
        total: res?.total
      }
    }

    return null
  }

  return {
    getStatsPools
  }
}
